<template>
  <div class="sdlogin">
    <!-- 盒子 -->
    <div class="sdbox">
      <!-- logo -->
      <div class="sdimg">
        <img src="logo.png" alt />
      </div>
      <!-- 登录页面 -->
      <div class="sdinput">
        <div class="sdpad">
          <el-input placeholder="请输入内容" v-model="username">
            <i slot="prefix" class="el-icon-user-solid"></i>
          </el-input>
        </div>
        <div class="sdpad">
          <el-input placeholder="请输入内容" v-model="password" show-password>
            <i slot="prefix" class="el-icon-lock"></i>
          </el-input>
        </div>
        <!-- 按钮 -->
        <div class="sdann">
          <el-row>
            <el-button type="primary" @click="login()">登录</el-button>
            <el-button type="info" >重置</el-button>
          </el-row>
        </div>
      </div>
      <!-- ------------- -->
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      username: "admin",
      password: "123456"
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    login() {
      axios
        .post("http://127.0.0.1:8888/api/private/v1/login", {
          username: this.username,
          password: this.password
        })
        .then(res => {
          console.log(res)
          if (res.data.meta.msg == "登录成功") {
               this.$message({
        message: "登录成功",
        type: "success"
      });
            sessionStorage.setItem("token", res.data.data.token);
            // that.globalData.token = res.data.data.token;
            this.$router.push("/Home/Welcome");
          } else {
      this.$message.error("用户错误或者密码错误");

          }
        });
    },
   
    
  },
  // 以下是生命周期钩子 注：没用到的钩子请自行删除
  /**
   * 在实例初始化之后，组件属性计算之前，如data属性等
   */
  beforeCreate() {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
   * 在挂载开始之前被调用：相关的 render 函数首次被调用。
   */
  beforeMount() {},
  /**
   * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
   * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
   */
  mounted() {},
  /**
   * 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
   * 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
   */
  beforeUpdate() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {},
  /**
   * 实例销毁之前调用。在这一步，实例仍然完全可用。
   */
  beforeDestroy() {},
  /**
   * Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，
   * 所有的事件监听器会被移除，所有的子实例也会被销毁。
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.sdann {
  position: absolute;
  right: 20px;
  bottom: 20px;
}
.sdpad {
  padding: 10px;
}
.sdinput {
  margin-top: 100px;
}
.el-icon-user-solid {
  padding-top: 12px;
  padding-left: 5px;
}
.el-icon-lock {
  padding-top: 12px;
  padding-left: 5px;
}
.sdimg {
  position: absolute;
  top: -75px;
  left: 32%;
  /* right: 50%; */
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0px 0px 10px #e2d8d8;
}
.sdimg > img {
  position: absolute;
  top: 12px;
  left: 12px;
  width: 125px;
  height: 125px;
  background: #ddd;
  border-radius: 50%;
}
.sdlogin {
  width: 100%;
  height: 100%;
  background: #2b4b6b;
  position: relative;
}
.sdbox {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-200px, -125px);
  width: 400px;
  height: 300px;
  background: #fff;
  padding: 10px;
  box-shadow: 2px 2px 4px #e2d8d8;
  border-radius: 4px;
}
</style>
