import request from './request'

// get请求方式
export function gets (url,data) {
    return request.get(url,{
        params:data
    })
}

// post请求方式
export function posts (url,data) {
    return request.post(url,data)
}

// put请求方式
export function put (url,data) {
    return request.put(url,data)
}

//用户列表
export function ajaxUserList(data) {
    return gets(`/api/private/v1/users?query=${data.query}&pagenum=${data.pagenum}&pagesize=${data.pagesize}`)
}


// 状态显示
export function ajaxChangeState(uId,type) {
    return put(`/api/private/v1/users/${uId}/state/${type}`)
}

// 添加用户
export function ajaxAddUsers(obj) {
        return posts(`/api/private/v1/users`,obj)
    }

// 修改用户信息
export function ajaxxg(id) {
    return put(`/api/private/v1/users/${id}`)
}

// 删除用户信息
export function ajaxsc(id) {
    return delete(`/api/private/v1/users/${id}`)
}

// 当前角色列表
//用户列表
export function ajaxGoodsList(data) {
    return gets(`/api/private/v1/goods?query=${data.query}&pagenum=${data.pagenum}&pagesize=${data.pagesize}`)
}

//用户列表
export function ajaxCategoriesList(data) {
    return gets(`/api/private/v1/categories?query=${data.query}&pagenum=${data.pagenum}&pagesize=${data.pagesize}`)
}


//用户列表
export function ajaxOrdersList(data) {
    return gets(`/api/private/v1/orders?query=${data.query}&pagenum=${data.pagenum}&pagesize=${data.pagesize}`)
}

export function ajaxRolesxg(data) {
    return put(`/api/private/v1/roles?id=${data.id}&roleName=${data.roleName}`)
}















