<template>
  <div>
    <!-- <h1>分类参数</h1> -->
    <!-- 面包屑 -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/Welcome' }">商品管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/roles' }">参数列表</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- =============================================================== -->
    <!-- {{tableData}} -->
    <div class="sdtable">
      <!-- 提示 -->
      <div class="sdzhuyi">
        <i class="el-icon-warning-outline"></i>
        <span>注意：只允许为第三级分类设置相关参数!</span>
      </div>
      <!-- ============================================================== -->
      <!-- 联级选择器 -->
      <div>
        <div class="block">
          <span class="demonstration">选择商品分类：</span>

          <el-cascader
            v-model="value"
            :options="options"
            :props="{ expandTrigger: 'hover' }"
            @change="handleChange"
          ></el-cascader>
        </div>

        <el-card class="box-card">
          <el-tabs type="border-card">
            <el-tab-pane label="用户管理">



 <el-row>
        <el-button type="primary">添加角色</el-button>
      </el-row>
       <div class="sdtable">
        <!-- 添加角色 -->
        <el-table :data="tableData" border style="width: 100%" stripe>
          <el-table-column type="index" label="" width="50"></el-table-column>

          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="roleName" label="角色名称" width="180"></el-table-column>
               <el-table-column prop="address" label="操作">
            <template slot-scope="scope">
              <el-tooltip
                :enterable="false"
                content="修改"
                placement="top"
                @click="dialogVisible = true"
              >
                <el-button
                  size="mini"
                  @click="showdigth(scope.row.id)"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="删除" placement="top">
                <el-button
                  @click="open(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>


















                
            </el-tab-pane>
            <el-tab-pane label="配置管理">

            </el-tab-pane>
          </el-tabs>
        </el-card>

        <!-- {{paramsData}} -->
      </div>
    </div>

    <!-- ================================================================== -->
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      tableData: [],
      isshow: false,
      paramsData: [],
      value: [],
      options: [
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
              children: [
                {
                  value: "",
                  label: ""
                }
              ]
            },
            {
              value: "",
              label: "",
              children: [
                {
                  value: "",
                  label: ""
                }
              ]
            }
          ]
        }
      ]
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    handleChange(value) {
      console.log(value);
    }
  },
  // 以下是生命周期钩子 注：没用到的钩子请自行删除
  /**
   * 在实例初始化之后，组件属性计算之前，如data属性等
   */
  beforeCreate() {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
   * 在挂载开始之前被调用：相关的 render 函数首次被调用。
   */
  beforeMount() {},
  /**
   * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
   * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
   */
  mounted() {
    this.$axios
      .get("http://127.0.0.1:8888/api/private/v1/categories")
      .then(res => {
        // console.log(res);
        this.paramsData = res.data.data;
      });
  },
  /**
   * 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
   * 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
   */
  beforeUpdate() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {},
  /**
   * 实例销毁之前调用。在这一步，实例仍然完全可用。
   */
  beforeDestroy() {},
  /**
   * Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，
   * 所有的事件监听器会被移除，所有的子实例也会被销毁。
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.block {
  margin-top: 20px;
  margin-bottom: 20px;
}
.sdtable {
  width: 100%;
  height: 100%;
  margin: 10px;
  padding: 10px;
  background: #fff;
}
.sdzhuyi {
  width: 96%;
  background-color: #e6a23c;
  color: #fff;
  border-radius: 4px;
}
.sdzhuyi > i {
  margin: 10px;
}
.sdzhuyi > span {
  font-size: 14px;
}
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 98%;
}
.sd-dbox {
  width: 100%;
  height: 100%;
  padding: 10px;
}
.sd-dtbox {
  width: 100%;
  background: #ddd;
  height: 100%;
  margin-top: 10px;
}
.sd-jtbox {
  width: 100%;
  background: red;
  height: 100%;
  margin-top: 10px;
}
</style>
