<template>
  <div>
    <h1>商品列表</h1>
    <!-- 面包屑 -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/Welcome' }">用户管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/users' }">用户列表</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- -------------------------------------------- -->
    <!-- users    table -->
    <div class="sdtable">
      <!-- 添加用户头  -->
      <div style="margin-top: 15px;" class="sdidf">
        <el-input
          @clear="userFn"
          clearable
          placeholder="请输入内容"
          v-model="goodsParams.query"
          class="input-with-select"
          size="60%"
        >
          <el-button @click="userFn" slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <!-- 按钮 -->
        <el-row>
          <el-button type="primary" @click="dialogFormVisible = true">添加商品</el-button>
        </el-row>
      </div>
      <!-- ----------------------------------------------------- -->
      <!-- ------------------------------ -->
      <!-- 斑马纹   表格 -->
      <div class="sdbody">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="goods_name" label="姓名" width="700"></el-table-column>
          <el-table-column prop="goods_price" label="商品价格(元)" width="150"></el-table-column>
          <el-table-column prop="goods_weight" label="商品重量" width="150"></el-table-column>
          <el-table-column prop="add_time" label="创建时间" width="150"></el-table-column>
          <el-table-column prop="address" label="操作" width="150">
            <template slot-scope="scope">
              <el-tooltip
                :enterable="false"
                content="修改"
                placement="top"
                @click="dialogVisible = true"
              >
                <el-button
                  size="mini"
                  @click="showdigth(scope.row.id)"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="删除" placement="top">
                <el-button
                  @click="open(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>

        <!-- ----------------------------- -->
        <!-- 分页器 -->
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="goodsParams.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="goodsParams.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ajaxGoodsList } from "../utils/api";
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      goodsParams: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      dialogVisible: false,
      value: false,
      totalNum: 0,
      tableData: [],

      dialogTableVisible: false,
      dialogFormVisible: false
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    async userFn() {
      let { data: res } = await ajaxGoodsList(this.goodsParams);
      //   console.log(res);
      this.tableData = res.data.goods;
      this.totalNum = res.data.total;
    },
    handleSizeChange(size) {
      this.goodsParams.pagesize = size;
      this.userFn();
    },
    handleCurrentChange(page) {
      this.goodsParams.pagenum = page;
      this.userFn();
    }
  },
  // 以下是生命周期钩子 注：没用到的钩子请自行删除
  /**
   * 在实例初始化之后，组件属性计算之前，如data属性等
   */
  beforeCreate() {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
   * 在挂载开始之前被调用：相关的 render 函数首次被调用。
   */
  beforeMount() {},
  /**
   * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
   * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
   */
  mounted() {
    this.userFn();
  },
  /**
   * 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
   * 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
   */
  beforeUpdate() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {},
  /**
   * 实例销毁之前调用。在这一步，实例仍然完全可用。
   */
  beforeDestroy() {},
  /**
   * Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，
   * 所有的事件监听器会被移除，所有的子实例也会被销毁。
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.sdidf {
  display: flex;
  margin: 20px;
}
.el-button {
  margin-left: 10px;
}
.el-input-group {
  line-height: normal;
  display: inline-table;
  width: 60%;
  border-collapse: separate;
  border-spacing: 0;
}
.sdtable {
  width: 100%;
  height: 100%;
  margin: 10px;
  padding: 10px;
  background: #fff;
}
</style>
