<template>
  <div class="home">
    <el-container>
      <!-- 头部 -->
      <el-header>
        <!-- 左侧 -->
        <div class="sdheaderimg">
          <img src="https://www.escook.cn/vue-shop/img/heima.b5a855ee.png" alt />
          <p class="sdheaderp">电商后台管理系统</p>
        </div>
        <!-- 右侧 -->
        <div>
          <template>
          <button class="sdtc"  @click="open">退出</button>
          </template>
        </div>
      </el-header>
      <!-- 内容 -->
      <el-container>
        <!-- 左侧导航 -->
        <el-aside width="200px" :style="{width:isCollapse?'64px':'200px'}">
          <div class="znavp" @click="isCollapse=!isCollapse" >
            |||
          </div>
       <el-menu 
       router
       :collapse-transition="false"
       :collapse="isCollapse" 
       unique-opened 
       background-color="#333744" 
       active-text-color="#409Eff"
       text-color="#fff"
       :default-active="activePath">
            <el-submenu :index="'/'+item.path" v-for="(item) in menus" :key="item.id">
              <template slot="title">
                <i :class="icon[item.id]"></i>
                <span slot="title" >{{ item.authName }}</span>
              </template>
              <el-menu-item @click="savePath(`/Home/${subitem.path}`)" :index="`/Home/${subitem.path}`" v-for="(subitem) in item.children" :key="subitem.id">
               <i class="el-icon-menu"></i>
                {{ subitem.authName }}
              </el-menu-item>
            </el-submenu>
          </el-menu> 
         
        </el-aside>
        <!-- 右侧导航 -->
        <el-main>
       

          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
// import axios from "axios";

export default {
  name: "Home",
  components: {},
  data() {
    return {
      isCollapse: false,                  // 不折叠
      menus: [],                          // 菜单列表
   icon:{
     "125":"el-icon-user-solid",
     "103":"el-icon-folder-add",
     "101":"el-icon-s-goods",
     "102":"el-icon-s-order",
     "145":"el-icon-s-marketing"
   },                                     // icon
   activePath:"",                         // 激活路由地址存储
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
 
     open() {
        this.$confirm('确定要退出吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          sessionStorage.removeItem("token");
      this.$router.push("/Login");
          this.$message({
            type: 'success',
            message: '退出成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });          
        });
      },
      savePath(path){
        window.sessionStorage.setItem("activePath",path)
        this.activePath=path
      }

  },
  watch:{
    $route(to,from){
      if(to.path=='/Welcome'){
        this.activePath=''
      }else{
        this.activePath=to.path
      }
      window.sessionStorage.getItem("activePath",to.activePath)
      
    }
  },
  created(){
  let path=window.sessionStorage.getItem("activePath")
  this.activePath=path
  },
  mounted() {
    // console.log(this);
    this.$axios.get("http://127.0.0.1:8888/api/private/v1/menus").then(res => {
      // console.log(res);
      this.menus = res.data.data;
    });
    // axios.get("http://127.0.0.1:8888/api/private/v1/menus")
    //   .then(res => {
    //     this.menus = res.data;
    //     this.menuss = res.data.children;

    //   });
  }
};
</script>
