<template>
  <div>
    <!-- <h1>角色列表</h1> -->
    <!-- 面包屑 -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/Welcome' }">权限管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/roles' }">角色列表</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- =============================================================== -->
    <!-- {{tableData}} -->
    <!-- 内容 -->
    <div class="sdtable">
      <!-- 添加角色 -->
      <el-row>
        <el-button type="primary">添加角色</el-button>
        <!-- ************************************************************************ -->
         </el-row>
      <div class="sdtable">
        <!-- 添加角色 -->
        <el-table :data="tableData" border style="width: 100%" stripe>
          <el-table-column type="index" label width="50"></el-table-column>

          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="roleName" label="角色名称" width="180"></el-table-column>
          <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
          <el-table-column prop="address" label="操作">
            <template slot-scope="scope">
              <el-tooltip
                :enterable="false"
                content="修改"
                placement="top"
                @click="dialogFormVisible = true"
              >
                <el-button
                  size="mini"
                  @click="showdigth(scope.row.id)"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="删除" placement="top">
                <el-button
                  @click="open(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="分配权限" placement="top">
                <el-button size="mini" type="warning" icon="el-icon-setting" circle></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!-- ================================================================ -->




















        
        <!-- 修改编辑用户

        <el-dialog title="提示" :visible.sync="dialogFormVisible" width="50%">
          -----------------------------------
          角色名称
          <el-form
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="用户名" prop="username">
              <el-input v-model="editForm.username"></el-input>
            </el-form-item>
          </el-form>
          -----------------------------------------------------

          角色描述
          <el-form
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="角色描述" prop="usersname">
              <el-input v-model="editForm.usersname"></el-input>
            </el-form-item>
          </el-form>
           ---------------------------------------------------------------

          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="editUsers">确 定</el-button>
          </span>
        </el-dialog> -->
      </div>
    </div>
  </div>
</template>

<script>
import { ajaxRolesxg } from "../utils/api";

export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      tableData: [],
      dialogVisible: false,
      dialogTableVisible: false,
      dialogFormVisible: false,
      editForm: {
        username: "",
        usersname: "",
      },
      rulesFn: {
        username: [
          { required: true, message: "请输入名称", trigger: "blur" },
          { min: 6, max: 20, message: "长度在 6 到 20 个字符", trigger: "blur" }
        ],
        usersname: [
          { required: true, message: "请描述", trigger: "blur" },
          { min: 6, max: 20, message: "长度在 6 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    //   tijiao修改用户方法
    editUsers() {
      this.$refs.editForm.validate(async valid => {
        // console.log(valid)/\
        if (!valid) return;
        const { data: res } = await this.$http.put(
          "users/" + this.editForm.id,
          {
            email: this.editForm.username,
            mobile: this.editForm.username
          }
        );
        if (res.meta.status !== 200) {
          return this.$message.error("xxx");
        }

        this.dialogFormVisible = false;
        this.userFn();
        this.$message.success("保存");
      });
    },
    async showdigth(data) {
      let { data: res } = await ajaxRolesxg(this.editForm);
      console.log(res);
      if (res.meta.status == 200) {
        // alert(111)
        this.editForm = res.data;
        this.$message({
          message: res.meta.msg,
          duration: 1000,
          type: "success"
        });
      } else {
        return this.$message.error("保存失败");
      }
      this.dialogVisible = true;
    },
    open() {
      this.$confirm("确定要删除吗", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          //   sessionStorage.removeItem("tableData.id");
          //    this.tableData.splice(index,1)
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消退出"
          });
        });
    }
  },
  // 以下是生命周期钩子 注：没用到的钩子请自行删除
  /**
   * 在实例初始化之后，组件属性计算之前，如data属性等
   */
  beforeCreate() {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
   * 在挂载开始之前被调用：相关的 render 函数首次被调用。
   */
  beforeMount() {},
  /**
   * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
   * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
   */
  mounted() {
    this.$axios.get("https://www.liulongbin.top:8888/api/private/v1/roles").then(res => {
      // console.log(res);
      this.tableData = res.data.data;
    });
  },
  /**
   * 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
   * 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
   */
  beforeUpdate() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {},
  /**
   * 实例销毁之前调用。在这一步，实例仍然完全可用。
   */
  beforeDestroy() {},
  /**
   * Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，
   * 所有的事件监听器会被移除，所有的子实例也会被销毁。
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.sdtable {
  width: 100%;
  height: 100%;
  margin: 10px;
  padding: 10px;
  background: #fff;
}
</style>
