<template>
  <div>
    <!-- <h1>用户列表</h1> -->
    <!-- 面包屑 -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/Welcome' }">用户管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/users' }">用户列表</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- -------------------------------------------- -->
    <!-- users    table -->
    <div class="sdtable">
      <!-- 添加用户头  -->
      <div style="margin-top: 15px;" class="sdidf">
        <el-input
          @clear="userFn"
          clearable
          placeholder="请输入内容"
          v-model="userParams.query"
          class="input-with-select"
          size="60%"
        >
          <el-button @click="userFn" slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <!-- 按钮 -->
        <el-row>
          <el-button type="primary" @click="dialogFormVisible = true">添加用户</el-button>
        </el-row>

        <el-dialog title="添加用户" :visible.sync="dialogFormVisible">
          <!-- 用户名 -->
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="用户名" prop="username">
              <el-input v-model="ruleForm.username"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->
          <!-- 密码 -->
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="密码" prop="password">
              <el-input v-model="ruleForm.password"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->
          <!-- 邮箱 -->
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="邮箱" prop="email">
              <el-input v-model="ruleForm.email"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->
          <!-- 电话 -->
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="电话" prop="mobile">
              <el-input v-model="ruleForm.mobile"></el-input>
            </el-form-item>
          </el-form>
          <!-- --------------------------------------------------------------- -->
          <!-- 确认或取消 -->
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
          </div>
        </el-dialog>
        <!-- ----------------------------------------------------- -->
      </div>
      <!-- ------------------------------ -->
      <!-- 斑马纹   表格 -->
      <div class="sdbody">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="username" label="姓名" width="110"></el-table-column>
          <el-table-column prop="email" label="邮箱"></el-table-column>
          <el-table-column prop="create_time" label="电话"></el-table-column>
          <el-table-column prop="role_name" label="角色"></el-table-column>
          <el-table-column prop="mg_state" label="状态">
            <template slot-scope="scope">
              <el-switch
                @change="changeState(scope.row.id,scope.row.mg_state)"
                v-model="scope.row.mg_state"
                active-color="#409EFF"
                inactive-color="#DCDFE6"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="操作">
            <template slot-scope="scope">
              <el-tooltip
                :enterable="false"
                content="修改"
                placement="top"
                @click="dialogVisible = true"
              >
                <el-button
                  size="mini"
                  @click="showdigth(scope.row.id)"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="删除" placement="top">
                <el-button
                  @click="open(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                ></el-button>
              </el-tooltip>
              <!-- <template>
                    <el-button type="text" @click="open">点击打开 Message Box</el-button>
              </template>-->
              <el-tooltip :enterable="false" content="分配权限" placement="top">
                <el-button size="mini" type="warning" icon="el-icon-setting" circle></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!-- 修改编辑用户 -->

        <el-dialog title="提示" :visible.sync="dialogVisible" width="50%">
          <!-- ----------------------------------- -->
          <!-- 用户名 -->
          <el-form
            disabled
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="用户名" prop="username">
              <el-input v-model="editForm.username"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->

          <!-- 邮箱 -->
          <el-form
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="邮箱" prop="email">
              <el-input v-model="editForm.email"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->
          <!-- 电话 -->
          <el-form
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="电话" prop="mobile">
              <el-input v-model="editForm.mobile"></el-input>
            </el-form-item>
          </el-form>
          <!--  --------------------------------------------------------------- -->

          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="editUsers">确 定</el-button>
          </span>
        </el-dialog>

        <!-- ----------------------------- -->
        <!-- 分页器 -->
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="userParams.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="userParams.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  ajaxUserList,
  ajaxChangeState,
  ajaxAddUsers,
  ajaxxg,
  ajaxsc
} from "../utils/api";
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    //   邮箱
    let checkEmail = (rule, value, callback) => {
      let em = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!value) {
        return callback(new Error("Email不能为空"));
      }
      if (em.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确的Email"));
      }
    };
    // 电话
    let checkMobile = (rule, value, callback) => {
      let phone = /^1[3|4|5|8][0-9]\d{4,8}$/;
      if (!value) {
        return callback(new Error("电话不能为空"));
      }
      if (phone.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确的电话号"));
      }
    };
    return {
      dialogVisible: false,
      value: false,
      totalNum: 0,
      tableData: [],

      userParams: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 18, message: "长度在 3 到 18 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 20, message: "长度在 6 到 20 个字符", trigger: "blur" }
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      ruleForm: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      editForm: {
        password: "",
        email: "",
        mobile: ""
      },
      rulesFn: {
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 20, message: "长度在 6 到 20 个字符", trigger: "blur" }
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      dialogTableVisible: false,
      dialogFormVisible: false
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    //   删除
    // async open(id){
    //     const confirmResult=await this.$confirm('确定要删除吗', '提示', {
    //       confirmButtonText: '确定',
    //       cancelButtonText: '取消',
    //       type: 'warning'
    //     }).catch(err=>err)
    //     if(confirmResult !=='confirm'){
    //         return this.$message.info('已取消删除')
    //     }
    //     const {data:res}=await ajaxsc(id)
    //      if (res.meta.status == 200) {

    //     this.$message({
    //       message: res.meta.msg,
    //       duration: 1000,
    //       type: "success"
    //     });
    //   }
    // },
    open() {
      this.$confirm("确定要删除吗", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          //   sessionStorage.removeItem("tableData.id");
          //    this.tableData.splice(index,1)
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消退出"
          });
        });
    },
    //   tijiao修改用户方法
    editUsers() {
      this.$refs.editForm.validate(async valid => {
        // console.log(valid)/\
        if (!valid) return;
        const { data: res } = await this.$http.put(
          "users/" + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        );
        if (res.meta.status !== 200) {
          return this.$message.error("xxx");
        }

        this.dialogVisible = false;
        this.userFn();
        this.$message.success("保存");
      });
    },

    async showdigth(id) {
      let { data: res } = await ajaxxg(id);
      console.log(res);
      if (res.meta.status == 200) {
        // alert(111)
        this.editForm = res.data;
        this.$message({
          message: res.meta.msg,
          duration: 1000,
          type: "success"
        });
      } else {
        return this.$message.error("保存失败");
      }
      this.dialogVisible = true;
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    // list列表
    async userFn() {
      let { data: res } = await ajaxUserList(this.userParams);
        console.log(res);
      this.tableData = res.data.users;
      this.totalNum = res.data.total;
    },
    handleSizeChange(size) {
      this.userParams.pagesize = size;
      this.userFn();
    },
    handleCurrentChange(page) {
      this.userParams.pagenum = page;
      this.userFn();
    },
    async submitUserAdd() {
      let { data: res } = await ajaxAddUsers(this.ruleForm);
      if (res.meta.status == 201) {
        this.$message({
          message: res.meta.msg,
          duration: 1000,
          type: "success"
        });
        this.dialogFormVisible = false;
        this.userFn();
      }
    },
    // 提交
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.submitUserAdd();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 滑块
    async changeState(uId, bool) {
      // console.log(bool)
      let { data: res } = await ajaxChangeState(uId, bool);
      // console.log(res)
      if (res.meta.status == 200) {
        this.$message({
          message: res.meta.msg,
          duration: 1000,
          type: "success"
        });
      }
    }
  },
  // 以下是生命周期钩子 注：没用到的钩子请自行删除
  /**
   * 在实例初始化之后，组件属性计算之前，如data属性等
   */
  beforeCreate() {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
   * 在挂载开始之前被调用：相关的 render 函数首次被调用。
   */
  beforeMount() {},
  /**
   * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
   * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
   */
  mounted() {
    this.userFn();
  },
  /**
   * 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
   * 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
   */
  beforeUpdate() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {},
  /**
   * 实例销毁之前调用。在这一步，实例仍然完全可用。
   */
  beforeDestroy() {},
  /**
   * Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，
   * 所有的事件监听器会被移除，所有的子实例也会被销毁。
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.el-button {
  margin-left: 10px;
}
.sdbody {
  margin: 10px;
}
.sdidf {
  display: flex;
  margin: 20px;
}
.el-input-group {
  line-height: normal;
  display: inline-table;
  width: 60%;
  border-collapse: separate;
  border-spacing: 0;
}
.sdtable {
  width: 100%;
  height: 100%;
  margin: 10px;
  padding: 10px;
  background: #fff;
}
</style>
