<template>
  <div>


    <div class="sdbody">
      <el-table :data="tableData" stripe style="width: 100%">
        <el-table-column type="index" label="#" width="50"></el-table-column>
        <el-table-column prop="cat_name" label="分类名称" width="110">
    <!-- <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree> -->

        </el-table-column>
        <el-table-column  prop="cat_deleted" label="是否有效">
            <i class="el-icon-success"></i>
        </el-table-column>
        <el-table-column prop="cat_level" label="排序"></el-table-column>
        <el-table-column prop="address" label="操作">
          <template slot-scope="scope">
            <el-tooltip
              :enterable="false"
              content="修改"
              placement="top"
              @click="dialogVisible = true"
            >
              <el-button
                size="mini"
                @click="showdigth(scope.row.id)"
                type="primary"
                icon="el-icon-edit"
                circle
              ></el-button>
            </el-tooltip>

            <el-tooltip :enterable="false" content="删除" placement="top">
              <el-button
                @click="open(scope.row.id)"
                size="mini"
                type="danger"
                icon="el-icon-delete"
                circle
              ></el-button>
            </el-tooltip>
            <!-- <template>
                    <el-button type="text" @click="open">点击打开 Message Box</el-button>
            </template>-->
            <el-tooltip :enterable="false" content="分配权限" placement="top">
              <el-button size="mini" type="warning" icon="el-icon-setting" circle></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 修改编辑用户 -->

      <el-dialog title="提示" :visible.sync="dialogVisible" width="50%">
        <!-- ----------------------------------- -->
        <!-- 用户名 -->
        <el-form
          disabled
          :model="editForm"
          ref="editForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="用户名" prop="username">
            <el-input v-model="editForm.username"></el-input>
          </el-form-item>
        </el-form>
        <!-- ----------------------------------------------------- -->
        <!-- 邮箱 -->
        <el-form :model="editForm" ref="editForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="editForm.email"></el-input>
          </el-form-item>
        </el-form>
        <!-- ----------------------------------------------------- -->
        <!-- 电话 -->
        <el-form :model="editForm" ref="editForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="电话" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <!--  --------------------------------------------------------------- -->

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>

      <!-- ----------------------------- -->
      <!-- 分页器 -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="categoriesParams.pagenum"
          :page-sizes="[1, 2, 5, 10]"
          :page-size="categoriesParams.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalNum"
        ></el-pagination>
      </div>

      
    </div>
  </div>
</template>


<script>
import { ajaxCategoriesList } from "../utils/api";
export default {
  data() {
    return {
      tableData: [],

      editForm: {
        password: "",
        email: "",
        mobile: ""
      },
      categoriesParams: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      dialogVisible: false,
      value: false,
      totalNum: 0,
      tableData: [],

      dialogTableVisible: false,
      dialogFormVisible: false,
      //   ==================================================
      data: [
        {
          label: "一级 1",
          children: [
            {
              label: "二级 1-1",
              children: [
                {
                  label: "三级 1-1-1"
                }
              ]
            }
          ]
        },
        {
          label: "一级 2",
          children: [
            {
              label: "二级 2-1",
              children: [
                {
                  label: "三级 2-1-1"
                }
              ]
            },
            {
              label: "二级 2-2",
              children: [
                {
                  label: "三级 2-2-1"
                }
              ]
            }
          ]
        },
        {
          label: "一级 3",
          children: [
            {
              label: "二级 3-1",
              children: [
                {
                  label: "三级 3-1-1"
                }
              ]
            },
            {
              label: "二级 3-2",
              children: [
                {
                  label: "三级 3-2-1"
                }
              ]
            }
          ]
        }
      ],
      defaultProps: {
        children: "children",
        label: "label"
      }
    };
  },
  mounted() {
    this.userFn();
  },
  methods: {
    async userFn() {
      let { data: res } = await ajaxCategoriesList(this.categoriesParams);
      console.log(res);
      this.tableData = res.data.result;
      this.totalNum = res.data.total;
    },
    handleSizeChange(size) {
      this.categoriesParams.pagesize = size;
      this.userFn();
    },
    handleCurrentChange(page) {
      this.categoriesParams.pagenum = page;
      this.userFn();
    },
    handleNodeClick(data) {
      console.log(data);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.el-icon-success{
    background: #045804;
    border-radius: 10px;
    color:aliceblue;
}
</style>
